  margin-bottom: 140px;
  padding-top: 85px;
}

.game {
  width: 25%;
}

.game__link {
  display: block;
  width: 100%;
}

.game__link:hover .game__title {
  color: var(--color-purple);
  transition: color 0.3s ease-in-out;
}

.game__bg {
  display: flex;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: 98%;
  background-repeat: no-repeat;
