}

.game-popup {
  position: fixed;
  left: 0;
  right: 0;
  bottom: -100vh;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  transition: bottom 0.5s;
}

.game-popup__body {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 30px;
  padding-top: 120px;
  width: 920px;
  height: 460px;
  background-color: var(--color-white);
}

.game-popup__close {
  border: none;
  padding: 0;
  background-color: transparent;
  position: absolute;
  right: 50px;
  top: 30px;
  width: 40px;
  height: 40px;
}

.game-popup__close span {
  position: absolute;
  left: 0;
  top: 50%;
  display: block;
  width: 60px;
  height: 2px;
  background-color: #b5b5b5;
  transform: translateY(-50%);
}

.game-popup__close span:first-child {
  transform: rotate(45deg);
}

.game-popup__close span:last-child {
  transform: rotate(-45deg);
}

.game-popup__title {
  margin-bottom: 30px;
  font-family: "Space Mono Normal";
  font-weight: 700;
  font-size: 60px;
  text-transform: uppercase;
}

.game-popup__links {
  display: flex;
}

.popup-open {
  bottom: 0;
  opacity: 1;
  visibility: visible;
  transition: bottom 0.5s;
}

.android-link {
  margin-right: 20px;
